<template>
  <div id="Taipei">
    <select class="custom-select custom-select-md" v-model="start">
      <option v-for="data in cata">{{data}}</option>
    </select>
    <table class="table table-bordered table-hover">
      <thead class="table-dark">
        <tr>
          <th scope="col">景點</th>
          <th scope="col">分類</th>
          <th scope="col">捷運</th>
          <th scope="col">地址</th>
          <th scope="col">營業資訊</th>
          <th scope="col">交通</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="data in datas" v-if="choseOne(data)">
          <th class="view" scope="row">{{data.view}}</th>
          <td class="cata">{{data.cata}}</td>
          <td class="mrt">{{data.MRT}}</td>
          <td class="add" @mouseover="showFunction(data)" @mouseout="hideFunction(data)">
            <div class="map" :class="data.id" :id="data.id" @mouseover="showFunction(data)"></div>
            <a href="#">{{data.add}}</a>
          </td>
          <td class="info">{{data.info}}</td>
          <td class="com">{{data.communication}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>

export default {
  name: "TaipeiTravel",
  created() {
    this.getview();
    // this.initMap()
  },
  data() {
    return {
      datas: [],
      latlng: [],
      cata: [
        "景點類別",
        "養生溫泉",
        "藍色公路",
        "	歷史建築",
        "藝文館所",
        "單車遊蹤",
        "戶外踏青",
        "宗教信仰",
        "其　　他"
      ],
      start: "景點類別"
    };
  },
  methods: {
    getview() {
      let xhr = new XMLHttpRequest();
      let self = this;
      xhr.open(
        "GET",
        "https://data.taipei/opendata/datalist/apiAccess?scope=resourceAquire&rid=36847f3f-deff-4183-a5bb-800737591de5"
      );
      xhr.onload = function() {
        let viewJson = JSON.parse(xhr.responseText);
        let getview = viewJson.result.results;
        // console.log(getview);
        getview.forEach(item => {
          self.datas.push({
            view: item.stitle,
            cata: item.CAT2,
            MRT: item.MRT,
            add: item.address,
            info: item.MEMO_TIME,
            communication: item.info,
            經度: item.longitude,
            緯度: item.latitude,
            id: "map" + item._id
          });
          // console.log(self.datas);
        });
      };
      xhr.send();
    },
    // initMap(){
    //   let self = this;
    //   self.datas.forEach(item => {
    //     let showId = "#" + item.id;
    //     var latlng = { lat: Number(item.緯度), lng: Number(item.經度) };
    //     var map = new google.maps.Map($(showId), {
    //       zoom: 11,
    //       center: latlng
    //     });
    //     var marker = new google.maps.Marker({
    //       position: latlng,
    //       map: map
    //     });
    //   });
    // },
    choseOne(item) {
      let self = this;
      if (self.start == "景點類別") {
        return true;
      } else if (item.cata == self.start) {
        return true;
      }
    },
    showFunction(item) {
      let showClass = "." + item.id;
      let showId = "#" + item.id;
      var latlng = { lat: Number(item.緯度), lng: Number(item.經度), };
      var map = new google.maps.Map($(showId), {
        zoom: 11,
        center: latlng
      });
      var marker = new google.maps.Marker({
        position: latlng,
        map: map
      });
      $(showClass).css("display", "block");
      console.log(typeof Number(item.緯度));
    },
    hideFunction(item) {
      let showClass = "." + item.id;
      $(showClass).css("display", "none");
      // console.log(showClass);
    }
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
tbody {
  border: solid 5px blue;
  width: 100%;
}
th,
td {
  height: 100%;
}
.view {
  width: 130px;
}
.cata {
  width: 90px;
}
.mrt {
  width: 75px;
}
.add {
  position: relative;
}
.map {
  position: absolute;
  /* background-color:black; */
  left: 50px;
  border: 1px black solid;
  width: 800px;
  height: 500px;
  display: none;
  /* display: block; */
}
</style>



